import { Component, OnInit } from '@angular/core';
import {ResetHttpService} from '../../../share/util/reset-http.service';
import {FormBuilder, FormControl, FormGroup, Validators} from '@angular/forms';
import {UtilServeService} from '../../../share/util/util-serve.service';
import {UrlServeService} from '../../../share/util/url-serve.service';
import {NzMessageService} from 'ng-zorro-antd';

@Component({
  selector: 'app-add-permission',
  templateUrl: './add-permission.component.html',
  styleUrls: ['./add-permission.component.scss']
})
export class AddPermissionComponent implements OnInit {
  validateForm: FormGroup;
  patt = /\w{1,100}/;
  allRole;
  constructor(private http: ResetHttpService,private fb: FormBuilder,private utilServe:UtilServeService,private url:UrlServeService,private msg:NzMessageService) {
    this.validateForm = this.fb.group({
      userName: [ '', [ this.checkName ] ],
      roles : [ '', [ Validators.required  ] ],
      imname:['',[Validators.required]]
    });
  }

  ngOnInit() {
    this.getRole();
  }
  //获取角色组
  getRole(){
    this.http.get(this.url.allRoles,{

    },data=>{
      // console.log(data);
      if(data.code ='001000'){
        this.allRole = data.response;
      }else{
        this.msg.error(data.message);
      }
    })
  }
//提交
  submitForm = ($event, value) => {
    var that = this;
    console.log(value,this.validateForm);
    $event.preventDefault();
    for (const key in this.validateForm.controls) {
      this.validateForm.controls[ key ].markAsDirty();
      this.validateForm.controls[ key ].updateValueAndValidity();
    }
    if(!this.validateForm.valid) return;
    this.http.get(this.url.permissionAddManger,{
      name:this.validateForm.controls['imname'].value,
      role:this.validateForm.controls['roles'].value,
      account:this.validateForm.controls['userName'].value
    },data=>{
      // console.log(data);
      if(data.code =='001000'){
        this.utilServe.initPassword();
      }else{
        this.msg.error(data.message);
      }
    });
  };
  //取消
  cancleForm(e: MouseEvent): void {
    history.back();
  }
  //验证用户名
  checkName = (control:FormControl): {[ s: string ]: boolean} =>{
    if(!control.value){
      return {required:true}
    }else if(!this.patt.test(control.value)){
      return { error: true };
    }
  }
  //确认

}
