import {Component, OnInit, ViewChild} from '@angular/core';
import {ResetHttpService} from '../../../share/util/reset-http.service';
import {NzMessageService, NzModalService} from 'ng-zorro-antd';
import {Router} from '@angular/router';
import {UrlServeService} from '../../../share/util/url-serve.service';
import {UtilServeService} from '../../../share/util/util-serve.service';
import {ModalRoleEditPermissionComponent} from '../modal-role-edit-permission/modal-role-edit-permission.component';

@Component({
  selector: 'app-role-manger-permission',
  templateUrl: './role-manger-permission.component.html',
  styleUrls: ['./role-manger-permission.component.scss']
})
export class RoleMangerPermissionComponent implements OnInit {
  @ViewChild('editRowTable') editRowTable;

  dataSet:any = [];
  patt = /\w{6,12}/;
  constructor(private util:UtilServeService,private http: ResetHttpService,private router:Router,private modalService:NzModalService,private url:UrlServeService,private msg:NzMessageService) {

  }

  ngOnInit() {
    this.getData();
  }
  //获取数据
  getData(){
    this.http.get(this.url.allRoles,{},data=>{
      // console.log(data);
      if(data.code == '001000'){
        this.dataSet = data.response;
      }else{
        this.msg.error(data.message);
      }
    },err=>{
      console.log(err);
    })
  }
  //新增角色
  addRole(){
    this.router.navigate(['/permission/add-role']);
  }


  //修改角色组
  createRoleModal(data){
    var modal = this.modalService.create({
      nzTitle: '重置密码',
      nzContent: ModalRoleEditPermissionComponent,
      nzClosable: false,
      nzComponentParams:data,
      nzFooter:null
    });
    modal.afterOpen.subscribe(() => console.log('[afterOpen] emitted!'));

    // Return a result when closed
    modal.afterClose.subscribe((result) => console.log('[afterClose] The result is:', result));
  }
  //删除
  deleteRow(id): void {
    this.http.get(this.url.deleRole,{
      ids:id
    },data=>{
      // console.log(data);
      if(data.code == '001000'){
        this.msg.success('删除成功！');
        this.getData();
      }else{
        this.msg.error(data.message);
      }
    })
  }
  //跳转
  goPage(option){
    this.util.goPage(option);
  }

}
