import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {PermissionMangerComponent} from './permission-manger/permission-manger.component';
import {AddPermissionComponent} from './add-permission/add-permission.component';
import {RoleAddPermissionComponent} from './role-add-permission/role-add-permission.component';
import {RoleMangerPermissionComponent} from './role-manger-permission/role-manger-permission.component';

const routes: Routes = [
  {path:'',component:PermissionMangerComponent,data:{breadcrumb: '权限管理'}},
  {path:'add-permission',component:AddPermissionComponent,data:{breadcrumb: '增加管理员'}},
  {path:'add-role',component:RoleAddPermissionComponent,data:{breadcrumb: '增加角色'}},
  {path:'roles',component:RoleMangerPermissionComponent,data:{breadcrumb: '角色管理'}}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class PermissionRoutingModule { }
