import {Component, OnInit, ViewChild} from '@angular/core';
import {Router} from '@angular/router';
import {ResetHttpService} from '../../../share/util/reset-http.service';
import {NzMessageService, NzModalService} from 'ng-zorro-antd';
import {ModalRpasswordPermissionComponent} from '../modal-rpassword-permission/modal-rpassword-permission.component';
import {ModalRolePermissionComponent} from '../modal-role-permission/modal-role-permission.component';
import {UrlServeService} from '../../../share/util/url-serve.service';
import {UtilServeService} from '../../../share/util/util-serve.service';

@Component({
  selector: 'app-permission-manger',
  templateUrl: './permission-manger.component.html',
  styleUrls: ['./permission-manger.component.scss']
})
export class PermissionMangerComponent implements OnInit {
  @ViewChild('editRowTable') editRowTable;

  dataSet:any = [];
  patt = /\w{6,12}/;
  constructor(private util:UtilServeService,private http: ResetHttpService,private router:Router,private modalService:NzModalService,private url:UrlServeService,private msg:NzMessageService) {

  }

  ngOnInit() {
    this.getData();
  }
  //获取数据
  getData(pnum='',pznum=''){
    this.http.get(this.url.permissionAllManger,{
      page:pnum,
      pageSize:pznum
    },data=>{
      console.log(data);
      if(data.code == '001000'){
        this.dataSet = data.response;
      }else{
        this.msg.error(data.message);
      }
    },err=>{
      console.log(err);
    })
  }
  //新增管理员
  addPermiss(){
    this.router.navigate(['/permission/add-permission']);
  }


//修改密码
  createModal(data): void {
    var modal = this.modalService.create({
      nzTitle: '重置密码',
      nzContent: ModalRpasswordPermissionComponent,
      nzClosable: false,
      nzComponentParams:data,
      nzFooter:null
    });
    modal.afterOpen.subscribe(() => console.log('[afterOpen] emitted!'));

    // Return a result when closed
    modal.afterClose.subscribe((result) => console.log('[afterClose] The result is:', result));
  }
  //修改角色组
  createRoleModal(data){
    var modal = this.modalService.create({
      nzTitle: '重置密码',
      nzContent: ModalRolePermissionComponent,
      nzClosable: false,
      nzComponentParams:data,
      nzFooter:null
    });
    modal.afterOpen.subscribe(() => console.log('[afterOpen] emitted!'));

    // Return a result when closed
    modal.afterClose.subscribe((result) => console.log('[afterClose] The result is:', result));
  }
  //删除
  deleteRow(id): void {
    this.http.get(this.url.permissionDeleManger,{
      ids:id
    },data=>{
      console.log(data);
      if(data.code == '001000'){
        this.msg.success('删除成功！');
        this.getData();
      }else{
        this.msg.error(data.message);
      }
    })
  }
  //跳转
  goPage(option){
    this.util.goPage(option);
  }

}
