import { Component, OnInit } from '@angular/core';
import {FormBuilder, FormControl, Validators} from '@angular/forms';
import {ResetHttpService} from '../../../share/util/reset-http.service';
import {NzMessageService, NzModalRef} from 'ng-zorro-antd';
import {UrlServeService} from '../../../share/util/url-serve.service';

@Component({
  selector: 'app-modal-role-permission',
  templateUrl: './modal-role-permission.component.html',
  styleUrls: ['./modal-role-permission.component.scss']
})
export class ModalRolePermissionComponent implements OnInit {
  patt = /\w{6,12}/;
  validateForm;
  information;
  allMenu;
  constructor(private http: ResetHttpService,private fb:FormBuilder,private nzmref:NzModalRef,private url:UrlServeService,private msg:NzMessageService) {
    this.validateForm = this.fb.group({
      roles   : [ '', [ Validators.required ] ],
      password: [ '', [ this.checkPassword ] ]
    });
  }

  ngOnInit() {
    this.information = this.nzmref.getContentComponent();
    console.log(this.information);
    this.getData();
  }
//验证密码
  checkPassword = (control:FormControl): {[ s: string ]: boolean} =>{
    if(!control.value){
      return {required:true}
    }else if(!this.patt.test(control.value)){
      return { error: true };
    }
  };
  //获取当前所有角色
  getData(){
    this.http.get(this.url.allRoles,{}
    ,data=>{
      // console.log(data);
      if(data.code == '001000'){
        this.allMenu = data.response;
      }else{
        this.msg.error(data.message);
      }
      },err=>{
      console.log(err);
      })
  }
  commit(){
    for (const key in this.validateForm.controls) {
      this.validateForm.controls[ key ].markAsDirty();
      this.validateForm.controls[ key ].updateValueAndValidity();
    }
    if(!this.validateForm.valid){
      return;
    }else{
      //请求数据
      this.http.get(this.url.permissionUpdateManger,{
        id:this.information.id,
        role:this.information.role
      },data=>{
        // console.log(data);
        if(data.code == '001000'){

        }else{
          this.msg.error(data.message);
        }
      },err=>{
        console.log(err);
      });
      this.nzmref.destroy({data:'success'});
    }
  }
  cancle() {
    this.nzmref.destroy({data: '取消'});
  }
}
